@import url(https://fonts.googleapis.com/css?family=Titillium+Web:400,700,600);
body {
  background: #ccc;
  font-family: "Titillium Web", sans-serif;
  font-weight: 400;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


a {
  text-decoration: none;
  display: inline-block;
}

#card {
  width: 50rem;
  margin: auto;
  overflow: hidden;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}

.search-box label {
  position: relative;
}
.search-box label:after {
  position: absolute;
  content: "";
  border-right: 0.625rem solid transparent;
  border-left: 0.625rem solid transparent;
  border-bottom: 0.625rem solid #fff;
  bottom: -1.0625rem;
  right: 50%;
  margin-right: -0.3125rem;
}
.search-box input[type=text] {
  width: 100%;
  font: 2rem "Titillium Web", sans-serif;
  color: #1a4e8e;
  border: 0;
  padding: 0.75rem 0.9375rem 0.9375rem;
  background: #e6ebed;
  border-radius: 0 0 8px 8px;
  text-align: center;
  box-sizing: border-box;
}

.notfound {
  background: #fff;
  padding: 1.875rem;
  text-align: center;
}
.notfound h2 {
  font-size: 2rem;
  color: #2368bd;
  margin-bottom: 0.625rem;
}
.notfound p {
  font-size: 1rem;
  color: #a3b6bb;
}

.github-profile {
  width: 100%;
}
.github-profile-info {
  background: rgb(222, 216, 230);
  text-align: center;
  padding: 3.5rem 0.9375rem;
  height: 30vh;
}

.github-profile-info img {
  width: 6.25rem;
  height: 6.25rem;
  border-radius: 50%;
  display: block;
  box-shadow: 0 0 0.0625rem rgba(0, 0, 0, 0.5);
}
.github-profile-info h2 a {
  font-size: 1.65rem;
  color: #2368bd;
}
.github-profile-info h3 {
  font-size: 1.1885rem;
  color: #60bace;
}
.github-profile-state {
  
  background: #457ce2;
  text-align: center;
  padding: 3rem 0.5rem;
  border-radius: 0 0 0.3125rem 0.3125rem;
  height: 15vh;
}
.github-profile-state ul {
  direction: ltr;
}
.github-profile-state li {
  list-style: none;
  display: inline-block;
  margin-right: 1rem;
}
.github-profile-state li:last-child {
  margin-right: 0;
}
.github-profile-state a {
  font-size: 1.5rem;
  color: #fff;
}
.github-profile-state a:hover {
  font-size: 2rem;
  color: rgb(233, 229, 238);
}
.github-profile-state i {
  font-size: 1.5rem;
  font-weight: 700;
  font-style: normal;
  display: block;
}
.github-profile-state span {
  font-size: 0.844rem;
  letter-spacing: 0.0625rem;
  color: #eceff3;
}

@media only screen and (max-width: 826px) {
  #card {
  width: 25rem;
  }
}
